.gradient-loader {
  background: transparent;
  padding: 12px 0;
  max-width: 90%;
  min-height: 35px; }
  .gradient-loader .animated-background {
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-name: placeHolderShimmer;
            animation-name: placeHolderShimmer;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    background: -webkit-gradient(linear, left top, right top, color-stop(8%, #eeeeee), color-stop(18%, #dddddd), color-stop(33%, #eeeeee));
    background: -webkit-linear-gradient(left, #eeeeee 8%, #dddddd 18%, #eeeeee 33%);
    background: -o-linear-gradient(left, #eeeeee 8%, #dddddd 18%, #eeeeee 33%);
    background: linear-gradient(to right, #eeeeee 8%, #dddddd 18%, #eeeeee 33%);
    background-size: 800px 104px;
    height: 35px;
    position: relative; }
  .gradient-loader .background-masker {
    background: #f2f2f2;
    position: absolute; }
  .gradient-loader .background-masker.top,
  .gradient-loader .background-masker.second-line,
  .gradient-loader .background-masker.third-line,
  .gradient-loader .background-masker.second-end,
  .gradient-loader .background-masker.third-end,
  .gradient-loader .background-masker.first-end {
    top: 40px;
    left: 0;
    right: 0;
    height: 6px; }
  .gradient-loader .background-masker.top {
    height: 60px;
    top: 0; }
  .gradient-loader .background-masker.first-end,
  .gradient-loader .background-masker.second-end,
  .gradient-loader .background-masker.third-end {
    width: auto;
    left: 16%;
    top: 0;
    height: 8px;
    right: 0; }
  .gradient-loader .background-masker.second-line {
    top: 8px; }
  .gradient-loader .background-masker.second-end {
    left: 45%;
    top: 14px; }
  .gradient-loader .background-masker.third-line {
    top: 22px; }
  .gradient-loader .background-masker.third-end {
    left: 75%;
    top: 27px; }

@-webkit-keyframes placeHolderShimmer {
  0% {
    background-position: -468px 0; }
  100% {
    background-position: 468px 0; } }

@keyframes placeHolderShimmer {
  0% {
    background-position: -468px 0; }
  100% {
    background-position: 468px 0; } }



